@import url(https://fonts.googleapis.com/css?family=Russo+One&display=swap);
* {
  font-family: Arial, Helvetica, sans-serif;
}
*:focus {
  outline: none;
}
.flex {
  display: flex;
}
.flex-center-main {
  justify-content: center;
}

.flex-center-cross {
  align-items: center;
}
.flex-space-around {
  justify-content: space-around;
}
.flex-space-between {
  justify-content: space-between;
}
.flex-space-evenly {
  justify-content: space-evenly;
}
.flex-end {
  justify-content: flex-end;
}
.flex-align-center {
  align-items: center;
}
.flex-column {
  flex-direction: column;
}

/**********************
**********************
        DETAIL STYLES
**********************
**********************/
.detailbar {
  background: #f8f8f8;
  flex-basis: 80%;
  height: auto;
}
header {
  background: #eeeeee;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 1;
}
header a {
  -webkit-text-decoration-line: none;
          text-decoration-line: none;
  color: #939faa;
}
header a:hover {
  -webkit-text-decoration-line: none;
          text-decoration-line: none;
  color: #2b4054;
}
header nav {
  padding-right: 1em;
}
header nav div {
  padding: 1em;
}
header nav img {
  width: 2em;
  height: 2em;
  border-radius: 100%;
}

.chart > div {
  box-shadow: 1em 1em 2em #dae5e9;
  margin: 3em;
}
.chart > div h3 {
  text-align: center;
  padding: 1em;
  margin: 0px;
  color: #2b4054;
  border-bottom: #a1bad1 1px solid;
}

[class='.chart .flex-center-main'] {
  flex-basis: 90%;
}
rect {
  fill: transparent;
}
.summary {
  background: #f7f7f7;
}
.summary-item {
  margin-top: 1em;
  margin-bottom: 1em;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.summary-item p {
  font-size: x-small;
}
.summary-item * {
  margin: 0;
  color: #2b4054;
}
.border {
  border-right: #2b4054 1px solid;
}
.h1 {
  display: block;
  font-size: 2em;
  font-weight: bold;
}
.table-home {
  height: 20em;
  overflow: scroll;
  width: 80%;

  margin: auto;
  border-radius: 1em;
  box-shadow: 0.2em 0.2em 1em #939faa;
  margin-bottom: 5em;
}
.table-title {
  background: #9cbcc6;
  margin: auto;
}
.table-item {
  margin: auto;
}
.table-wrapper {
  margin-top: 5em;
}
.table-wrapper h3 {
  text-align: center;
  padding: 1em;
  margin: auto;
  margin-bottom: 1em;
  width: 80%;
  color: #2b4054;
}
.table-wrapper div > p {
  flex-basis: 20%;
}
.table-wrapper a {
  text-decoration: none;
  color: #33485c;
}
.hover:hover {
  background: #9cbcc6;
  color: #f7f7f7;
}
.hover {
  border-bottom: #939faa 1px solid;
}
.single-trip-button {
  padding: 0.5em 1em;
  border-radius: 2em;
  border: none;
  background: #607b94;
  box-shadow: 0.2em 0.2em 1em #dae5e9;
  cursor: pointer;
}
.button-info {
  font-size: 1.3em;
  color: rgb(226, 231, 223);
}

a {
  color: white;
  -webkit-text-decoration-line: none;
          text-decoration-line: none;
}
.trip-page {
  padding: 2em;
}
.trip-page h1 {
  text-align: center;
}

.trip {
  border-radius: 2em;
  margin: 1em;
  padding: 1em;
  box-shadow: 0.5em 0.5em 2em #a1aebb;
}
.trip-link {
  margin: auto;
}
.link {
  margin: 2em;
  border-radius: 1em;
  color: #eeeeee;
  font-size: 1em;
  padding: 0.5em;
  cursor: pointer;
  background: #33485c;
  border: none;
  box-shadow: 0.2em 0.2em 1em #33485c;
}

.link:hover {
  background: rgba(49, 72, 92, 0.6);
  color: rgb(214, 231, 231);
  -webkit-transition: ease-in-out 0.5s;
  transition: ease-in-out 0.5s;
  -webkit-transform: scale(1.2) translate3d(2em);
          transform: scale(1.2) translate3d(2em);
}

/**********************
**********************
        SIDEBAR STYLES          
**********************
**********************/
.sidebar {
  background: #2b4054;
  flex-basis: 20%;
  padding: 0.1em;
  height: auto;
  color: #f8f8f8;
}
.logo {
  width: 3em;
  height: 3em;
  border-radius: 100%;
  margin: 0em 1em;
}
.logo-link {
  text-decoration: none;
}
.logo-name {
  font-family: 'Russo One';
  color: #fff;
}

.Admin {
  width: 5em;
  height: 5em;
  border-radius: 100%;
  margin: 0em 1em;
}
.admin-logo {
  margin-top: 3em;
  margin-bottom: 2em;
}
.admin-logo p {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-weight: 500;
}
.overview {
  text-transform: uppercase;
  cursor: pointer;
}
details > summary::-webkit-details-marker {
  display: none;
}
.left-nav {
  align-items: baseline;
  padding: 1em;
}
.left-nav:hover {
  background: #33485c;
}

details {
  text-align: center;
  flex-basis: 80%;
}

details > div {
  text-align: left;
  padding: 0.5em;
}
details > a:hover {
  background: #2b4054;
}
details > p:hover {
  background: #2b4054;
}

